.container {
  height: 100%;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

page {
  min-height: 100%;
  flex: 1;
  font-family: -apple-system-font,Helvetica Neue,Helvetica,sans-serif;
  overflow: hidden;
  line-height: 1.6;
}

.two_line {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.tpl-weekly-item {
  padding: 15rpx;

  .week-icon {
    border: 1rpx solid #0bf;
    float: left;
    border-radius: 10rpx;
    font-size: 30rpx;
    color: #0bf;
    padding: 0 8rpx;
    margin: 10rpx 0 0 10rpx;
  }

  .text {
    margin-left: 80rpx;
  }

  .title {
    font-size: 35rpx;
  }

  .desc {
    font-size: 28rpx;
    color: #888;
    margin-right: 50rpx;
  }

  .desc-no {
    color: #ff5151;
  }
}
// 多行文本输入框

.textarea {
  background-color: #fff;
  border-top: 1rpx solid #c0c0c0;
  border-bottom: 1rpx solid #c0c0c0;
  width: 100%;
  font-size: 32rpx;
  color: #111;

  & > textarea {
    width: 690rpx;
    left: 30rpx;
    margin: 20rpx 0;
    color: inherit;
    font-size: inherit;
  }
}

.input {
  background-color: #fff;
  border: 1rpx solid #c0c0c0;
  border-radius: 10rpx;
  max-width: 100%;
  font-size: 32rpx;
  color: #111;
  padding: 10rpx 20rpx;
  margin: 20rpx auto;
}
